import "./Task.css";
import React from "react";
import { useState, useEffect } from "react";
import ListItems from "./ListItem";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
import { useAuth } from "./context/AuthContext";
import { Card, Button, Alert } from "react-bootstrap";
import { Link, useHistory } from "react-router-dom";
library.add(faTrash);
function Task() {
  const [error, setError] = useState("");
  const { currentUser, logout } = useAuth();
  const history = useHistory();

  async function handleLogout() {
    setError("");

    try {
      await logout();
      history.push("/login");
    } catch {
      setError("Failed to log out");
    }
  }

  let [currentItem, setCurrentItem] = useState({ text: "", key: "" });
  let [tasks, setTasks] = useState([]);
  const addTaskHandler = (e) => {
    e.preventDefault();
    if (currentItem.text) {
      console.log(tasks);
      tasks.push(currentItem);
      setTasks([...tasks]);
      setCurrentItem({ text: "", key: "" });
    }
  };

  const deleteItemHandler = (item) => {
    const updatedList = tasks.filter((i) => i.key !== item);
    setTasks([...updatedList]);
  };

  const editItemHandler = (item, value) => {
    const index = tasks.findIndex((i) => i.key == item);
    tasks[index].text = value;
    setTasks([...tasks]);
  };

  return (
    <div>
      <h2 className="text-center mb-4"> To do list</h2>

      <p>
        Current User : <span>{currentUser.email}</span>{" "}
      </p>
      <div className="App">
        <header>
          <form onSubmit={(e) => addTaskHandler(e)} id="form-list">
            <input
              onChange={(e) =>
                setCurrentItem({ text: e.target.value, key: Date.now() })
              }
              value={currentItem.text}
              type="text"
              placeholder="Enter Text"
            />
            <button type="submit"> Add</button>
          </form>
        </header>
        <ListItems
          editItem={editItemHandler}
          deleteItem={deleteItemHandler}
          items={tasks}
        ></ListItems>
      </div>
      <Card>
        <div className="w-100 text-center mt-2">
          <Button variant="link" onClick={handleLogout}>
            Log Out
          </Button>
        </div>{" "}
      </Card>
    </div>
  );
}

export default Task;
